@import url('https://fonts.googleapis.com/css2?family=Caveat:wght@600&display=swap');

*{
    margin: 0px;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Caveat', cursive;
}

.App{
    display: flex;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}

.Sidebar{
    background-color: #7C99AC;
    width: 20%;
    min-width: 200px;
}

.Main{
    width: 100%;
    height: 100%;
    /* background-image: url("./images/aka.png"); */
    background-image: linear-gradient(to right,#92A9BD,#FFEFEF );
    background-repeat: no-repeat;
    background-size: contain;
}